import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ActivityTestService {

  private testtActivityData = new BehaviorSubject<any>(null);
  testactivitydatacast = this.testtActivityData.asObservable();

  private testtActivityStudentData = new BehaviorSubject<any>(null);
  testactivitystudentdatacast = this.testtActivityStudentData.asObservable();

  constructor() { }

  getTestActivityData(){
    return this.testtActivityData.value
  }

  setTestActivityData(value){
    this.testtActivityData.next(value);
  }

  getTestActivityStudentData(){
    return this.testtActivityStudentData.value
  }

  setTestActivityStudentData(value){
    this.testtActivityStudentData.next(value);
  }

}
