import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { ActivityTestService } from './activity-test.service';


@Injectable({
  providedIn: 'root'
})
export class ActivityTestItemResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private activityTestService: ActivityTestService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.activityTestService.getTestActivityData();

    if(data == null){

      // Navigate to parent url
      this.router.navigateByUrl('/activity/test/list');

    }


    return;

  }

}

