import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityTestItemResolver } from './activity-test.resolver';

import { ActivityTestComponent } from './activity-test.component';

const routes: Routes = [
  { path: 'list', component: ActivityTestComponent},
  { path: 'list/details/:id',
    loadChildren: () => import('src/app/modules/admin/activity/activity-test/activity-test-item/activity-test-item.module').then(m => m.ActivityTestItemModule),
    resolve: {
      ActivityTestItemResolver
    }
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityTestRoutingModule { }
